.dashboardContent {
}
.list {
  display: flex;
  justify-content: space-between;

  flex-wrap: wrap;
}
.list__item {
  padding: 30px;
  flex: 0 0 30%;
  margin-bottom: 30px;
  border: 2px solid black;
  box-shadow: 0 0 30px 0px #000000;
}

.list__item h3 {
  font-size: 20px;
  font-weight: 700;
  text-align: center;
  padding-bottom: 10px;
  font-family: 'Ubuntu';
}

.list__item p {
  font-size: 16px;
  font-weight: 500;
  text-align: center;
  font-family: 'Ubuntu';
}

